html, body, div, span, applet, object, iframe, h1, h2, h3,
h4, h5, h6, p, blockquote, pre, a, abbr, acronym, address, big,
cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small, strike,
strong, sub, sup, tt, var, b, u, i, center, dl, dt, dd, ol, ul, li,
fieldset, form, label, legend, table, caption, tbody, tfoot,
thead, tr, th, td, article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup, menu, nav, output,
ruby, section, summary, time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure, footer,
header, hgroup, menu, nav, section {
  display: block;
}
body {
  line-height: 1;
}
ol, ul, li {
  list-style: none;
}
blockquote, q {
  quotes: none;
}
blockquote:before, blockquote:after, q:before, q:after {
  content: '';
  content: none;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}
a {
  text-decoration: none;
}
* {
  -o-box-sizing: border-box;
  box-sizing: border-box;
}



.logo {
  margin-top: 200px;
  font: 700 25px/1.2 'Georgia', 'Arial', 'Helvetica', sans-serif;
  display: flex;
  justify-content: center;
}

.main {
  margin-top: 10px;
}
.header {
	width: 100%;
	display: inline-flex;
	justify-content: space-around;
	align-items: center;
	height: 55px;
	border-bottom: 1px solid rgb(46, 47, 48);
	padding: 0 50px;
}

.header-logo {
	font: 400 20px 'Georgia', 'Arial', 'Helvetica', sans-serif;
	color: rgb(0, 0, 0);	
}

.header-nav {
	width: 500px;
	display: inline-flex;
	justify-content: space-around;
}

.header-nav-tab {
	font: 400 20px 'Georgia', 'Arial', 'Helvetica', sans-serif;
  text-decoration: none;
  color: rgb(0, 0, 0);
	transition: text-shadow 0.5s linear;
}

.nav--active {
	color: rgb(199, 37, 37);
}

.header-nav-tab:hover {
	text-shadow: 0 0 10px rgb(143, 143, 143);
	/* border-bottom: 1px solid rgb(143, 143, 143); */
}

.btnOnChangeLanguage {
	font: 400 18px 'Georgia', 'Arial', 'Helvetica', sans-serif;
	color: rgb(0, 0, 0);
	background-color: rgb(255, 255, 255);
	text-transform: uppercase;
	width: 80px;
	height: 40px;
	border: 0;
	padding: 0;
	cursor: pointer;
	margin: 0 1px;
	transition: text-shadow 0.5s linear;
}

.btnOnChangeLanguage:hover {
	color: rgb(199, 37, 37);
	text-shadow: 0 0 10px rgb(143, 143, 143);
}

